import {fetch as fetchPolyfill} from 'whatwg-fetch';
window.fetch = fetchPolyfill;

import { Component } from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';


import { Router, RouterEvent, NavigationEnd } from '@angular/router';

import { AuthenticationService } from './services/authentication/authentication.service';
import { OfflineService } from './services/offline/offline.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {
    public appPages = [
        {
            title: 'Home',
            url: '/home',
            icon: 'home'
        },
        {
            title: 'List',
            url: '/list',
            icon: 'list'
        }
    ];

    constructor(private platform: Platform, private splashScreen: SplashScreen, private statusBar: StatusBar, 
        private authenticationService: AuthenticationService, private router: Router, private iab: InAppBrowser,
        private offlineService:OfflineService){
        
        this.initializeApp();
    }

    initializeApp() {
        this.platform.ready().then(() => {
            
            this.statusBar.styleDefault();
            if (this.platform.is("android")) {
                this.statusBar.backgroundColorByHexString("#DDDDDD");
                this.statusBar.styleDefault();
            }
			
			
            this.authenticationService.authState.subscribe((state:any) => {
                if (state && state.data && state.data.authenticated) { 
                    this.router.navigate(['']);
                } else if (state && state.data && !state.data.authenticated) {
                    this.router.navigate(['login']);
                }
            }); 				
            
            
            setTimeout(() => {this.splashScreen.hide(); },1000);
		
			
			
        });
		
    }
    
    public openLink(link){
        this.iab.create(link, '_system');
    }    
    
}
