import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import {AuthGuardService} from './services/authentication/auth-guard.service';

const routes: Routes = [
    {
        path: '',
        redirectTo: 'datapoints',
        pathMatch: 'full',
        canActivate: [AuthGuardService]
    },
    {
        path: 'datapoints',
        loadChildren: () => import('./pages/datapoints/datapoints.module').then(m => m.DatapointsPageModule),
        canActivate: [AuthGuardService]
    },
    {
        path: 'datapoints/:id',
        loadChildren: () => import('./pages/datapoint/datapoint.module').then(m => m.DatapointPageModule),
        canActivate: [AuthGuardService]
    },
    { path: 'login', loadChildren: './pages/login/login.module#LoginPageModule' },
  {
    path: 'user-settings',
    loadChildren: () => import('./pages/user-settings/user-settings.module').then( m => m.UserSettingsPageModule)
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
