import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';

@Component({
    selector: 'app-datapoints-popover',
    templateUrl: './datapoints-popover.component.html',
    styleUrls: ['./datapoints-popover.component.scss'],
})
export class DatapointsPopoverComponent implements OnInit {

    constructor(public popoverController: PopoverController) { }

    ngOnInit() {}
    
    public selectOption(option){
        this.popoverController.dismiss({ data: option });
    }

}
