import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class AuthenticationService {
    
    authState = new BehaviorSubject({});
    
    constructor(public http: HttpClient, public storage: Storage, private platform: Platform) {    
    
        this.platform.ready().then(() => {
            this.ifLoggedIn();
        });    
 
    }

  
    ifLoggedIn() {
        this.storage.get('datanow_token').then((response) => {
            if (response) {
              this.authState.next({data:{authenticated:true}});
            }
            else{
                this.authState.next({data:{authenticated:false}});
            }
        }).catch(() => {
            this.authState.next({data:{authenticated:false}});
        });
    }    
    
    
    public isAuthenticated() {
        let state:any = this.authState.value;
        

        if (state && state.data && state.data.authenticated) { 
            return true;
        }         
        
        return false;
        
        
    }
    
    public refreshToken(){
        return new Promise((resolve, reject) => {
            this.storage.get("datanow_refresh").then((token) => {            
                if (token){            

                    this.http.post(environment.apiUrl + "/refresh", {token:token}).subscribe((res) => {
                         
                        this.storage.set("datanow_refresh", res["refresh_token"]).then(() => {});    
                        this.storage.set("datanow_token", res["access_token"]).then(() => {
                            resolve(res["access_token"]); 
                        });

                                              
                        
                                           
                    },(e) => {
                        reject(e);
                    }); 
                     
                }
                else{reject();} 
            });                    
        })       
    } 
  
    public login(data){
        return new Promise((resolve, reject) => {
            this.http.post(environment.apiUrl + "/authenticate", data).subscribe((res) => {

                    this.storage.set("datanow_token", res["access_token"]).then(() => {
                        
                        this.authState.next({data:{authenticated:true}});
                    });
                    
                    this.storage.set("datanow_refresh", res["refresh_token"]).then(() => {});                    
                    this.storage.set("datanow_user", res["user"]).then(() => {}); 
                    
                    resolve(res["access_token"]);             
              },(e) => {
              //console.log(e);
                  reject(e);
              });          
        })
    }


    public logout(){
        return new Promise((resolve, reject) => {
            
            this.storage.get("datanow_token").then((token) => {
                
                
                            
                if (token){
                    this.http.post(environment.apiUrl + "/logout",{}).subscribe((res) => {
                        
                        
                        resolve();
                        
                    }, (e) => {
                        resolve();
                    });   
                }
                else{
                    resolve();
                }
                
                
                this.storage.clear();
                this.authState.next({data:{authenticated:false}});
            });       
        })        
    }  

    public changePassword(email,password,passwordConfirm){
        return new Promise((resolve, reject) => {
            this.storage.get("datanow_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/users/me?token=" + token,{email:email,password:password}).subscribe((res) => {
                        resolve();                     
                    },(e) => {
                        reject(e);
                    });   
                }
                else{reject();} 
            });                    
        })       
    }
 
  
    
      
}
