import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';
import { Network } from '@ionic-native/network/ngx';

@Injectable({
    providedIn: 'root'
})
export class OfflineService {
    
    networkState = new BehaviorSubject(false);
    public connectionStatus:boolean;
    public heartbeatActive:boolean;
    public heartbeat:any;
    public syncedRequests:number;

    constructor(public http: HttpClient, public storage: Storage, public network:Network, public platform: Platform) { 
        
        
        this.connectionStatus = (this.network.type && this.network.type !== "none") || !this.platform.is("cordova");
        this.connectionChanged(this.connectionStatus);
        this.heartbeatActive = false;
        
        this.network.onConnect().subscribe(data => {
            this.connectionStatus = true;
            this.connectionChanged(this.connectionStatus);
            setTimeout(() => {this.doRequests();},5000);
        }, error => {
            //console.error(error)
        });

        this.network.onDisconnect().subscribe(data => {
            this.connectionStatus = false;
            this.connectionChanged(this.connectionStatus);
            this.startHeartbeat();
        }, error => {
            //console.error(error)
        });  
        
        this.platform.ready().then(() => {
            if (this.connectionStatus){
                this.doRequests();
            }
        });         
        
        
        
    }
    
    
    public doRequests(){
        this.storage.get("datanow_offlinedata").then((data:Array<any>) => {
            if (data){
                
                let requests = data.filter(function( obj ) {
                    return obj.synced !== true;
                });                
                
                
                this.syncedRequests = 0;
                for (var index of requests){
                    this.doRequest(index,requests);
                }
            }
        })
    }
    
    private doRequest(index, requests){
        let request = requests[index];
        this.http.post(request.endpoint, request.data).subscribe((res) => {
            request.synced = true;
            this.syncedRequests += 1;
            
            if (this.syncedRequests >= requests.length){
                this.storage.set("datanow_offlinedata", requests);
            }
            
        }, (e) => {
            this.syncedRequests += 1;
            
            if (this.syncedRequests >= requests.length){
                this.storage.set("datanow_offlinedata", requests);
            }        
        });         
    }
    
    public startHeartbeat(){
        if (this.heartbeatActive){return;}
        

        this.heartbeatActive = true;
        this.heartbeat = setInterval(() => {
            this.http.post(environment.apiUrl, {test: "test"}).subscribe((res) => {
                this.connectionStatus = true;
                this.connectionChanged(this.connectionStatus);
                this.doRequests();
                clearInterval(this.heartbeat);
                this.heartbeatActive = false;
                this.doRequests();
            });
            
        },20000); 
    }    
    
    
    
    public monitorConnection(){
        return this.networkState.asObservable();
    }   
    
    public connectionChanged(status){
        this.networkState.next(status);
    }     
    
    
    
    
}
