import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';

import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { IonicStorageModule } from '@ionic/storage';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { AuthInterceptor } from './services/authentication/auth-interceptor.service';

import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { Network } from '@ionic-native/network/ngx';
import { ChartModule } from 'angular-highcharts';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';

import { AccountPopoverComponent } from './components/account-popover/account-popover.component';
import { DatapointsPopoverComponent } from './components/datapoints-popover/datapoints-popover.component';

@NgModule({
    declarations: [AppComponent, AccountPopoverComponent, DatapointsPopoverComponent],
    entryComponents: [AccountPopoverComponent, DatapointsPopoverComponent],
    imports: [
        BrowserModule,
        IonicModule.forRoot({
            rippleEffect: false,
            mode: 'ios'
        }),
        FormsModule,
        AppRoutingModule,
        IonicStorageModule.forRoot(),
        HttpClientModule,
        ChartModule
    ],
    providers: [
        StatusBar,
        SplashScreen,
        Network,
        { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
        {
            provide: HTTP_INTERCEPTORS,
            useClass: AuthInterceptor,
            multi: true
        },
        InAppBrowser
    ],
    bootstrap: [AppComponent]
})
export class AppModule {}
